import React,{useState} from 'react';

const Pagination = ({ postsPerPage, totalPosts, paginate }) => {
  
  const pageNumbers = [];


  for (let i = 1; i <= Math.ceil(totalPosts /postsPerPage); i++) {
    pageNumbers.push(i);
  }



const [state,setState]=useState(
  {
    startIndex : 0, 
    endIndex : 3
  }
)
const prevPage = () => {
    setState(prevState => ({
        startIndex : prevState.startIndex - 3,
        endIndex : prevState.endIndex - 3
    }));
}

const nextPage = () => {
    setState(prevState => ({
        startIndex : prevState.startIndex + 3,
        endIndex : prevState.endIndex + 3
    }));
}












  return (
    <nav className="p-0 m-0 mb-3">
      <ul className='pagination p-0 m-0 '>
       {state.startIndex<=0?(<button disabled className="btn d-flex page-link rounded-circle"
          onClick={()=>prevPage()}
        >
          <i className="fa fa-angle-double-left"></i>
          </button>):( <button className="btn d-flex page-link rounded-circle"
          onClick={()=>prevPage()}
        >
          <i className="fa fa-angle-double-left"></i>
          </button>)}

        {pageNumbers.slice(state.startIndex,state.endIndex).map(number => (
          <li key={number} 
          className='d-flex justify-content-center  align-items-center page-item mx-1 p-0 m-0'>
            <button 
            onClick={() => paginate(number)} className='btn btn-secondary d-flex page-link rounded-circle'>
              {number}
            </button>
          </li>
        ))}
        {state.endIndex>=totalPosts/postsPerPage?(<button disabled className="btn d-flex page-link rounded-circle"
          onClick={()=>nextPage()}
        >
          <i className="fa fa-angle-double-right"></i>
          </button>):(<button className="btn d-flex page-link rounded-circle"
          onClick={()=>nextPage()}
        >
          <i className="fa fa-angle-double-right"></i>
          </button>)}
      </ul>
    </nav>
  );
};

export default Pagination;
